.navigation__button {
  display: none;
}
@media (max-width: 767px) {
  .navigation__button--js {
    display: flex;
    width: 24px;
    height: 14px;
    border: none;
    background-color: transparent;
    background-image: url("../img/svg/burger.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

@media (max-width: 767px) {
  .navigation__container--js {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background-color: rgba(1, 28, 64, 0.8);
  }
}

@media (max-width: 767px) {
  .navigation__content {
    display: grid;
    grid-template-columns: repeat(2, auto);
    gap: 33px;
    padding: 20px 15px;
    width: 100%;
    background-color: #f9fbfd;
  }
  .navigation__content--js {
    padding-bottom: 100vh;
    width: 89%;
    margin-left: auto;
  }
}

.navigation__logo {
  display: none;
}
@media (max-width: 767px) {
  .navigation__logo--js {
    display: block;
  }
}

.navigation__button-close {
  display: none;
}
@media (max-width: 767px) {
  .navigation__button-close--js {
    margin-left: auto;
    display: flex;
    width: 24px;
    height: 14px;
    border: none;
    background-color: transparent;
    background-image: url("../img/svg/burger-close.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

.navigation__list {
  display: grid;
