.form form {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.form input:not([type=checkbox]) {
  width: 100%;
  padding: 11px 16px;
  border: none;
  background-color: #f9fbfd;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 21px;
  color: #011c40;
}
.form input:not([type=checkbox])::placeholder {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 21px;
  color: rgba(1, 28, 64, 0.7);
}
.form input:not([type=checkbox]):hover {
  opacity: 0.8;
}
.form input:not([type=checkbox]):focus-visible {
  outline: 1px solid #f9fbfd;
  outline-offset: 2px;
  opacity: 1;
}

.form__checkbox input {
  width: auto;
}

.form__checkbox span {
  display: block;
  padding-left: 13px;
  margin-bottom: 30px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  font-style: normal;
  line-height: 13px;
  color: #f9fbfd;
}

.form__checkbox label {
  display: flex;
  align-items: center;
}

.form__checkbox-mark {
  position: relative;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
.form__checkbox-mark::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 8px;
  background-image: url("../img/svg/checkbox.svg");
  background-repeat: no-repeat;
  background-position: center;
  display: none;
}

.form__checkbox input:checked + .form__checkbox-mark::before {
  display: block;
}

.form__checkbox input:focus-visible + .form__checkbox-mark {
  outline: 1px solid #f9fbfd;
  outline-offset: 2px;
}

.form__button {
  width: 100%;
}